<template>
  <div>
    <div class="form">
      <h1>Apoを作成する！</h1>
      <p>{{time}}</p>
      <ul>
        <li><span>pricing</span><input v-model="pricing" type="number"></li>
        <div class="block">
          <span class="demonstration">Default</span>
          <el-date-picker
            v-model="meeting_time"
            type="datetime"
            placeholder="Select date and time">
          </el-date-picker>
        </div>
        <li><button @click="createAppointment"type="button" name="button">作成する</button></li>
      </ul>
    </div>
    <div class="message" v-if="message">{{message}}</div>
    <p>{{this.succed_log}}</p>
    <div v-html="insertApoLink"></div>
  </div>
</template>

<script>
export default {
  name: 'create-appointment',
  data() {
    return {
      account: null,
      message: "Let's create new Appointment",
      pricing: 1,
      meeting_time: Date.now(),
      latitude: 100,
      longitude: 200,
      time: null,
      succed_log: {
        id: null,
        sender_account: null,
        meeting_time: null,
        etherAmount: null,
        pricing: null,
        finished: null,
        lat: null,
        lng: null
      }
    }
  },
  mounted() {
  },
  methods: {
    createAppointment() {
      var meeting_time = new Date(this.meeting_time).getTime() / 1000
      this.message = "Transaction started";
      this.$store.state.contractInstance().createAppointment(meeting_time, this.latitude, this.longitude, {
        from: this.$store.state.web3.coinbase,
        value: web3.toWei(this.pricing, 'ether')
      })
      .then((r) => {
        this.message = "Transaction done"
        var succed_log = r["logs"][0].args
        this.succed_log.id = succed_log.id.toNumber()
        this.succed_log.sender_account = succed_log.sender_account
        this.succed_log.meeting_time = succed_log.meeting_time.toNumber()
        this.succed_log.etherAmount = succed_log.etherAmount.toNumber()
        this.succed_log.pricing = succed_log.pricing.toNumber()
        this.succed_log.finished = succed_log.finished
        this.succed_log.lat = succed_log.lat.toNumber()
        this.succed_log.lng = succed_log.lng.toNumber()
      })
      .catch((e) => {
        console.log(e)
        this.message = "Transaction faile"
      })
    }
  },
  computed: {
    insertApoLink() {
      var html = '<a href="#/appointment/' + this.succed_log.id + '">"アポ' + this.succed_log.id + '"</a>"'
      console.log(html)
      return html
    }
  }
}
</script>
