<template>
  <ul>
    <li>id : {{this.apo.id}}</li>
    <li>owner : {{this.apo.owner}}</li>
    <li>meeting_time : {{this.apo.meeting_time}}</li>
    <li>現在時刻 : {{this.apo.time}}</li>
    <li>etherAmount : {{this.apo.etherAmount}}</li>
    <li>pricing : {{this.apo.pricing}}</li>
    <li>finished : {{this.apo.finished}}</li>
    <li>latitude : {{this.apo.latitude}}</li>
    <li>longitude : {{this.apo.longitude}}</li>
  </ul>
</template>

<script>
export default {
  name: 'show-appointment',
  data() {
    return {
      apo: {
        id: null,
        owner: null,
        meeting_time: null,
        etherAmount: null,
        pricing: null,
        finished: null,
        latitude: null,
        longitude: null,
        time: null
      },
    }
  },
  props: ['id'],
  methods: {
    get_apo(id) {
      this.$store.state.contractInstance().getAppointment.call(id)
      .then((r) => {
        var apo = {}
        apo["id"] = r[0].toNumber()
        apo["owner"] = r[1]
        apo["meeting_time"] = new Date(r[2].toNumber() * 1000)
        apo["time"] = new Date()
        apo["etherAmount"] = web3.fromWei(r[3].toNumber(), 'ether')
        apo["pricing"] = web3.fromWei(r[4].toNumber(), 'ether')
        apo["finished"] = r[5]
        apo["latitude"] = r[6].toNumber()
        apo["longitude"] = r[7].toNumber()
        this.apo = apo
      })
      .catch((e) => {
      })
    },
    check_already_bet() {
      this.$store.state.contractInstance().userBetToAppointment.call()
    }
  },
  watch: {
    id: function() {
      this.get_apo(this.id)
    }
  }
}
</script>

<style lang="css">
li {
  list-style: none;
}
</style>
