<template>
  <div>
    <h1></h1>
    <show-appointment :id="last_id"></show-appointment>
    <achieved-to-appointment :id="last_id"></achieved-to-appointment>
  </div>
</template>

<script>
import ShowAppointment from '@/components/ShowAppointment';
import AchievedToAppointment from '@/components/AchievedToAppointment'

export default {
  name: 'last-appointment',
  components: {
    'show-appointment': ShowAppointment,
    'achieved-to-appointment': AchievedToAppointment
  },
  data() {
    return {
      promise_count: null,
      last_id: ""
    }
  },
  created() {
    this.get_user_appointment_count()
  },
  methods: {
    get_user_appointment_count(){
      this.$store.state.contractInstance().userToAppointmentCount.call(this.$store.state.web3.coinbase)
      .then((r) => {
        this.promise_count = r.toNumber();
        if (0 < this.promise_count) {
          this.get_last_appointment_id()
        }
      })
      .catch((e) => {
      })
    },
    get_last_appointment_id() {
      var promise_last_position = this.promise_count - 1;
      this.$store.state.contractInstance().userToAppointmentId.call(this.$store.state.web3.coinbase, promise_last_position)
      .then((r) => {
        this.last_id = r.toNumber()
      })
      .catch((e) => {
      })
    }
  }
}
</script>

