<template>
  <div class="withdraw-appointment">
    <withdraw-appointment :finished="apo_finished" :last_id="last_id" :status="apo_status"></withdraw-appointment>
    <p>{{show_finished_message}}</p>
    <p>{{show_status_message}}</p>
  </div>
</template>

<script>
import WithdrawAppointment from '@/components/WithdrawAppointment'

export default {
  name: 'withdraw_appointment',
  data() {
    return {
      apo_finished: null,
      apo_status: null,
      last_id: 0
    }
  },
  components: {
    'withdraw-appointment': WithdrawAppointment
  },
  created() {
    this.checkFinished()
    this.checkStatus()
  },
  methods: {
    checkFinished() {
      this.$store.state.contractInstance().checkAppointmentAlreadyFinised.call(0)
      .then((r) => {
        ;
        var finished = r[2]
        if (finished == true) {
          this.apo_finished = true
        } else {
          this.apo_finished = false
        }
      })
      .catch((e) => {
        ;
      })
    },
    checkStatus() {
      this.$store.state.contractInstance().checkUserToAppointStatus.call(0)
      .then((r) => {
        ;
        var apo_status = r.toNumber();
        if (apo_status == 0) {
          // 達成できず
          this.apo_status = 0
        } else if (apo_status == 1) {
          // 達成、未引き出し
          this.apo_status = 1
        } else {
          // 達成、引き出し済み
          this.apo_status = 2
        }
      })
      .catch((e) => {
        ;
      })
    }
  },
  computed: {
    show_finished_message() {
      if (this.apo_finished == true) {
        return "時間が過ぎました。"
      } else if (this.apo_finished == false) {
        return "時間前です。"
      } else {
        return "nullです"
      }
    },
    show_status_message() {
      if (this.apo_status == 0) {
        return "未達成です。"
      } else if (this.apo_status == 1) {
        return "達成しました！未引き出しです！"
      } else {
        return "達成しました！引き出し済みです！"
      }
    }
  }
}
</script>

