<template>
  <div class="show_appointment">
    <p>私の総Appointmentは{{this.promise_count}}アポです</p>
    <div v-for="promise_id in promise_ids">
      <router-link :to="'/appointment/' + promise_id ">アポ{{promise_id}}</router-link>
    </div>
    <p>userのアドレスは {{$route.params.address}}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      promise_count: null,
      promise_ids: []
    }
  },
  created() {
    this.get_user_appointment_count()
  },
  methods: {
    get_user_appointment_count(){
      this.$store.state.contractInstance().userToAppointmentCount.call(this.$store.state.web3.coinbase)
      .then((r) => {
        this.promise_count = r.toNumber();
        if (0 < this.promise_count) {
          this.get_appointment_id()
        }
      })
      .catch((e) => {
      })
    },
    get_appointment_id() {
      var promise_count = this.promise_count;
      var array = []
      for (var i = 0; i < this.promise_count; i++) {
        this.$store.state.contractInstance().userToAppointmentId.call(this.$store.state.web3.coinbase, i)
        .then((r) => {
          this.promise_ids.push(r.toNumber())
        })
        .catch((e) => {
        })
      }
    }
  }
}
</script>

